<template>
       <div class="header">
        <h2>Organization Manager</h2>
    </div>
    <hr>
    <div class="body">
        <div class="actions">
           <div class="row">
            <div class="col-sm-1">
                <label for="filter">Filter by</label>
            </div>
            <div class="col-1">
               <select name="" id="" class="form-select-sm">
                      <option value="">All</option>
                      <option value="">Active</option>
                      <option value="">Inactive</option>
               </select>
            </div>
            <div class="col-4"></div>
            <div class="col-3">
                <input type="text" placeholder="Search ..." class="form-control">
            </div>
            <div class="col">
              <p class="action-button">
                <button class="btn btn-outline-primary " v-bind:title="'Create'" data-bs-toggle="modal" data-bs-target="#create-modal">
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-person-plus-fill" viewBox="0 0 16 16">
                        <path d="M1 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H1zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6z"/>
                        <path fill-rule="evenodd" d="M13.5 5a.5.5 0 0 1 .5.5V7h1.5a.5.5 0 0 1 0 1H14v1.5a.5.5 0 0 1-1 0V8h-1.5a.5.5 0 0 1 0-1H13V5.5a.5.5 0 0 1 .5-.5z"/>
                      </svg>
                      Create
                </button>
                <button class="btn btn-outline-danger " v-bind:title="'Delete'">
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-person-x-fill" viewBox="0 0 16 16">
                        <path fill-rule="evenodd" d="M1 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H1zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm6.146-2.854a.5.5 0 0 1 .708 0L14 6.293l1.146-1.147a.5.5 0 0 1 .708.708L14.707 7l1.147 1.146a.5.5 0 0 1-.708.708L14 7.707l-1.146 1.147a.5.5 0 0 1-.708-.708L13.293 7l-1.147-1.146a.5.5 0 0 1 0-.708z"/>
                      </svg>
                      Remove
                </button>
              </p>
            </div>
            <hr>
            <div class="table-responsive-sm">
                <table class="table table-striped table-bordered table-hover">
                    <thead>
                        <th><input type="checkbox"  id="check"></th>
                        <th>User</th>
                        <th>Resource</th>
                        <th>View</th>
                        <th>Edit</th>
                        <th>Delete</th>
                        <th>Action</th>
                    </thead>
                    <tbody>
                        <tr v-for="item in permissions" :key="item">
                            <td class="col">
                                <input type="checkbox"  id="check">
                            </td>
                            <td class="col">{{findUsernameById(item.user.userId)}}</td>
                            <td class="col">{{item.resource.name}}</td>
                            <td class="col">
                                <span :class="{'badge text-bg-success':item.view==true,'badge text-bg-danger':item.view==false}">
                                    {{item.view}}
                                </span>
                            </td>
                            <td class="col">
                                <span :class="{'badge text-bg-success':item.edit==true,'badge text-bg-danger':item.edit==false}">
                                    {{item.edit}}
                                </span>
                            </td>
                            <td class="col">
                                <span :class="{'badge text-bg-success':item.delete==true,'badge text-bg-danger':item.delete==false}">
                                    {{item.delete}}
                                </span>
                            </td>
                            <td>
                                <span>
                                    <button class="btn btn-outline-primary" v-bind:title="'Edit'" data-bs-toggle="modal" data-bs-target="#staticBackdrop">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-pen-fill" viewBox="0 0 16 16">
                                                <path d="m13.498.795.149-.149a1.207 1.207 0 1 1 1.707 1.708l-.149.148a1.5 1.5 0 0 1-.059 2.059L4.854 14.854a.5.5 0 0 1-.233.131l-4 1a.5.5 0 0 1-.606-.606l1-4a.5.5 0 0 1 .131-.232l9.642-9.642a.5.5 0 0 0-.642.056L6.854 4.854a.5.5 0 1 1-.708-.708L9.44.854A1.5 1.5 0 0 1 11.5.796a1.5 1.5 0 0 1 1.998-.001z"/>
                                            </svg>
                                        </button>
                                        <button class="btn btn-outline-danger" v-bind:title="'Delete'" @click.prevent="deletePermission(item.permission)">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-trash3" viewBox="0 0 16 16">
                                                <path d="M6.5 1h3a.5.5 0 0 1 .5.5v1H6v-1a.5.5 0 0 1 .5-.5ZM11 2.5v-1A1.5 1.5 0 0 0 9.5 0h-3A1.5 1.5 0 0 0 5 1.5v1H2.506a.58.58 0 0 0-.01 0H1.5a.5.5 0 0 0 0 1h.538l.853 10.66A2 2 0 0 0 4.885 16h6.23a2 2 0 0 0 1.994-1.84l.853-10.66h.538a.5.5 0 0 0 0-1h-.995a.59.59 0 0 0-.01 0H11Zm1.958 1-.846 10.58a1 1 0 0 1-.997.92h-6.23a1 1 0 0 1-.997-.92L3.042 3.5h9.916Zm-7.487 1a.5.5 0 0 1 .528.47l.5 8.5a.5.5 0 0 1-.998.06L5 5.03a.5.5 0 0 1 .47-.53Zm5.058 0a.5.5 0 0 1 .47.53l-.5 8.5a.5.5 0 1 1-.998-.06l.5-8.5a.5.5 0 0 1 .528-.47ZM8 4.5a.5.5 0 0 1 .5.5v8.5a.5.5 0 0 1-1 0V5a.5.5 0 0 1 .5-.5Z"/>
                                            </svg>
                                        </button>
                                </span>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
           </div>
        </div>
    </div>
<!-- Modal create -->
<div class="modal fade" id="create-modal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="create-modal" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="staticBackdropLabel">Modal title</h5>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">
       <form @submit.prevent="createPermission">
        <div class="form-floating mb-3">
            <select  id="floatingInput" class="form-select" v-model="permission.user.userId">
                <option selected disabled>Select User</option>
                <option v-for="item in users" :key="item" v-bind:value="item.userId">{{item.username}}</option>
            </select>
            <label for="floatingInput">User</label>
        </div>
        <div class="form-floating mb-3">
            <select  id="floatingInput" class="form-select" v-model="permission.resource.resourceId">
                <option selected disabled>Select Resource</option>
                <option v-for="item in resources" :key="item" v-bind:value="item.resourceId">{{item.name}}</option>
            </select>
            <label for="floatingInput">Resource</label>
        </div>
        <div class="form-floating mb-3">
            <select  id="floatingInput" class="form-select" v-model="permission.view">
                <option selected disabled>View</option>
                <option value="true">True</option>
                <option value="false">False</option>
            </select>
            <label for="floatingInput">View</label>
        </div>
        <div class="form-floating mb-3">
            <select  id="floatingInput" class="form-select" v-model="permission.edit">
                <option selected disabled>Edit</option>
                <option value="true">True</option>
                <option value="false">False</option>
            </select>
            <label for="floatingInput">Edit</label>
        </div>
        <div class="form-floating mb-3">
            <select  id="floatingInput" class="form-select" v-model="permission.delete"> 
                <option selected disabled>Delete</option>
                <option value="true">True</option>
                <option value="false">False</option>
            </select>
            <label for="floatingInput">Delete</label>
        </div>
        <div class="row">
            <div class="col-2">
                <button class="btn btn-outline-success">Create</button>
            </div>
        </div>
       </form>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" id="close">Close</button>
      </div>
    </div>
  </div>
</div>
</template>
<script>
import axios from 'axios';
export default{
    name: "PermissionManager",
    data(){
        return{
            permissions: [],
            permission: {
                user: {
                    userId: ''
                },
                resource: {
                    resourceId: ''
                },
                view: '',
                edit: '',
                delete: ''
            },users : [],
            resources: []
        }
    },
    methods: {
        getPermissions(){
            axios.defaults.headers.common['Authorization'] = "Bearer "+ localStorage.getItem('token');
            console.log(document.cookie['token']);
            axios.get('/api/permission/get',document.cookie)
            .then(response => {
                this.permissions = response.data;
            })
            .catch(error => {
                console.log(error);
                this.$toast.error('Permission denied');
            })
        },
        createPermission(){
            axios.post('/api/permission/save', this.permission)
            .then(response => {
                response;
                this.getPermissions();
                this.$toast.success(`Thêm mới thành công`);
                document.getElementById('close').click();
            })
            .catch(error => {
                console.log(error);
            })
        },
        getAllUser(){
            axios.get('/api/user/get')
            .then(response => {
                this.users = response.data;
            })
            .catch(error => {
                console.log(error);
            })
        },
        getAllResource(){
            axios.get('/api/resource/get')
            .then(response => {
                this.resources = response.data;
            })
            .catch(error => {
                console.log(error);
            })
        }
        ,
        deletePermission(id){
            axios.delete('/api/permission/delete/' + id,document.cookie)
            .then(response => {
                response;
                this.getPermissions();
            })
            .catch(error => {
                console.log(error);
            })
        },findUsernameById(id){
            let username = '';
            this.users.forEach(item => {
                if(item.userId == id){
                    username = item.username;
                }
            })
            return username;
        },findResourceNameById(id){
            let name = '';
            this.resources.forEach(item => {
                if(item.resourceId == id){
                    name = item.name;
                }
            })
            return name;
        }
    },
    mounted(){
        this.getPermissions();
        this.getAllUser();
        this.getAllResource();
    }
}
</script>
<style>
table .col{
    padding-top: 1rem;
}
</style>